<template id="popup">
  <vs-popup fullscreen :title="title" :active.sync="activeInternal">
    <vs-table class="mb-base" :data="transferTypeTableData" :noDataText="transferTypeTableNoDataText" :sst="true" search @search="filterTransferTypeTable" @sort="orderTransferTypeTable" @selected="selectRow">
      <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
        <div class="flex flex-wrap-reverse items-center">
          <vs-button class="p-3 mr-4" @click="loadTransferTypeTableData(popupRef)" type="filled" icon="fa-sync" icon-pack="far" :disabled="transferTypeTableBusy">{{ $t('REFRESH') }}</vs-button>
        </div>

        <div class="flex justify-end mr-4">
          <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
            <span class="mx-2">{{ transferTypeTablePagingInfo }}</span>
            <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
              <span class="far fa-angle-down text-primary"></span>
              <vs-dropdown-menu>
                <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="transferTypeTablePerPage = item">
                  <span>{{ item }}</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
        </div>
      </div>

      <template slot="thead">
        <vs-th sort-key="typeId">
          {{ this.$t('ID') }}
        </vs-th>
        <vs-th sort-key="typeName">
          {{ this.$t('TYPE_NAME') }}
        </vs-th>
        <vs-th sort-key="externalTypeId">
          {{ this.$t('EXTERNAL_ID') }}
        </vs-th>
      </template>

      <template slot-scope="{data}">
        <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
          <vs-td :data="data[index].typeId">
            {{ data[index].typeId }}
          </vs-td>
          <vs-td :data="data[index].typeName">
            {{ data[index].typeName }}
          </vs-td>
          <vs-td :data="data[index].externalTypeId">
            {{ data[index].externalTypeId | placeholder('-') }}
          </vs-td>
        </vs-tr>
      </template>
    </vs-table>

    <vs-pagination :total="transferTypeTableTotalPages" v-model="transferTypeTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
</vs-popup>
</template>

<script>
import PopupMixin from '@/mixins/commons/PopupMixin'
import FundsTransferTypeTableMixin from '@/mixins/ft/FundsTransferTypeTableMixin'

export default {
  name: 'funds-transfer-type-popup',
  mixins: [PopupMixin, FundsTransferTypeTableMixin],
  mounted: function () {
    this.loadTransferTypeTableData(this.popupRef)
  },
  beforeDestroy: function () {
    this.$el.remove()
  }
}
</script>
