import Endpoint from '@/services/Endpoint'

export default {
  transferTypes (payload) {
    return Endpoint().get('/s1/ft/fundsTransferType', {
      params: payload
    })
  },
  createTransferType (payload) {
    return Endpoint().post('/s1/ft/fundsTransferType', payload.formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
  },
  modifyTransferType (payload) {
    return Endpoint().post('/s1/ft/fundsTransferType/update', payload.formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
  },
  transferTypeTree (payload) {
    return Endpoint().get('/s1/ft/fundsTransferType/tree', payload)
  }
}
