import FundsTransferTypeService from '@/services/ft/FundsTransferTypeService'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let TransferTypeTableMixin = {
  mixins: [DisabledClassMixin, LoadingMixin, ListLayoutMixin],
  props: {
    defaultTransferTypeTableFilter: {
      type: String,
      default: null
    },
    defaultTransferTypeTableOrderBy: {
      type: String,
      default: null
    },
    typeBehaviourEnumId: {
      type: String,
      default: null
    },
    disabled: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      transferTypeTableData: [],
      transferTypeTableFilterTimer: null,
      transferTypeTableFilter: this.defaultTransferTypeTableFilter,
      transferTypeTableOrderBy: this.defaultTransferTypeTableOrderBy,
      transferTypeTableContainer: 'loading-container',
      transferTypeTableBusy: false,
      transferTypeTableTotalRows: 0,
      transferTypeTableCurrentPage: 1,
      transferTypeTablePerPage: 10
    }
  },
  computed: {
    transferTypeTableNoDataText: function () {
      return this.transferTypeTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    transferTypeTableTotalPages: function () {
      return Math.ceil(this.transferTypeTableTotalRows / this.transferTypeTablePerPage)
    },
    transferTypeTablePagingInfo: function () {
      return this.pagingInfo(this.transferTypeTableCurrentPage, this.transferTypeTablePerPage, this.transferTypeTableTotalRows)
    }
  },
  methods: {
    loadTransferTypeTableData (loadingContainerId) {
      let self = this
      this.transferTypeTableBusy = true
      this.transferTypeTableContainer = loadingContainerId || this.transferTypeTableContainer
      this.startLoading(this.transferTypeTableContainer)
      FundsTransferTypeService.transferTypes({
        pageIndex: this.transferTypeTableCurrentPage - 1,
        pageSize: this.transferTypeTablePerPage,
        filter: this.transferTypeTableFilter,
        orderByField: this.transferTypeTableOrderBy,
        typeBehaviourEnumId: this.typeBehaviourEnumId,
        disabled: this.disabled
      }).then(function (response) {
        self.stopLoading(self.transferTypeTableContainer)
        self.transferTypeTableBusy = false
        self.transferTypeTableTotalRows = response.data.totalRows
        self.transferTypeTableData = response.data.transferTypeList || []
      }).catch(function (error) {
        self.stopLoading(self.transferTypeTableContainer)
        self.transferTypeTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshTransferTypeTable () {
      this.loadTransferTypeTableData()
    },
    filterTransferTypeTable (filter) {
      let self = this
      if (this.transferTypeTableFilter !== filter) {
        clearTimeout(this.transferTypeTableFilterTimer)
        this.transferTypeTableFilterTimer = setTimeout(() => {
          self.transferTypeTableFilter = filter
          self.loadTransferTypeTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.transferTypeTableFilterTimer)
      }
    },
    orderTransferTypeTable (orderBy, active) {
      this.transferTypeTableOrderBy = (active ? '' : '-') + orderBy
      this.loadTransferTypeTableData()
    }
  },
  watch: {
    transferTypeTableCurrentPage: function () {
      this.refreshTransferTypeTable()
    },
    transferTypeTablePerPage: function () {
      this.refreshTransferTypeTable()
    },
    typeBehaviourEnumId: function () {
      this.refreshTransferTypeTable()
    }
  }
}

export default TransferTypeTableMixin
